import { Table, Input, Button, Label } from "reactstrap";
import { useParams, Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import axios from "axios";
import { url } from "./config";
const DetailForm = () => {
  const { sect, num } = useParams();
  const [board, setBoard] = useState({
    num: null,
    subject: "",
    content: "",
    writedate: "",
    fileurl: "",
    writer: "",
    viewcount: null,
    likecount: null,
  });
  const [images, setImages] = useState([]);
  const [heart, setHeart] = useState(false);
  const navigate = useNavigate();
  useEffect(() => {
    axios
      .get(`${url}/boarddetail/${sect}/${num}`)
      .then((res) => {
        console.log(res);
        setBoard(res.data.board);
        let fileurl = res.data.board.fileurl;
        if (fileurl != null && fileurl !== "") {
          let filenums = fileurl.split(",");
          setImages([...filenums]);
        }
        setHeart(res.data.heart);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const boardModify = (boardNum) => {
    navigate(`/modifyform/${boardNum}`);
  };
  const selectBoard = (e) => {
    axios.get(`${url}/boardlike/${num}`).then((res) => {
      console.log(res.data);
      setBoard({ ...board, likecount: res.data.likeCount });
      setHeart(res.data.isSelect);
    });
  };
  return (
    <>
      <h5 style={{ textAlign: "center", margin: "20px auto" }}></h5>
      <div
        style={{
          margin: "0 auto",
          width: "600px",
          border: "1px solid lightgray",
          borderRadius: "7px",
          padding: "10px",
        }}
      >
        <Table borderless>
          <tbody>
            <tr>
              <td>
                <Label for="writer"></Label>
              </td>
              <td>
                <Input
                  type="text"
                  name="writer"
                  id="writer"
                  disabled
                  value={board.writer}
                  required="required"
                />
              </td>
            </tr>
            <tr>
              <td>
                <Label for="subject"> </Label>
              </td>
              <td>
                <Input
                  name="subject"
                  type="text"
                  id="subject"
                  disabled
                  value={board.subject}
                />
              </td>
            </tr>
            <tr>
              <td>
                <Label for="content"> </Label>
              </td>
              <td>
                <Input
                  type="textarea"
                  id="content"
                  name="content"
                  disabled
                  cols="40"
                  rows="15"
                  value={board.content}
                />
              </td>
            </tr>
            <tr>
              <td>
                <Label for="file"></Label>
              </td>
              <td>
                {images.length !== 0 &&
                  images.map((num) => (
                    <img
                      key={num}
                      src={`${url}/img/${num}`}
                      width={"120px"}
                      height={"100px"}
                      alt="  "
                      style={{ marginRight: "10px" }}
                    />
                  ))}
              </td>
            </tr>
            <tr>
              <td></td>
              <td>
                <img
                  src={heart ? "/like.png" : "/unlike.png"}
                  alt=""
                  width={"20px"}
                  onClick={selectBoard}
                />
                &nbsp;
                {board.likecount}
                &nbsp;
                <Button color="primary" onClick={() => boardModify(board.num)}>
                  
                </Button>
                &nbsp;&nbsp;
                <Button color="primary" tag="a" href="/list">
                  
                </Button>
              </td>
            </tr>
          </tbody>
        </Table>
      </div>
    </>
  );
};
export default DetailForm;
