import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";
import { useState } from "react";
import { url } from "./config";
const Main = () => {
  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => {
    setIsOpen(!isOpen);
  };
  return (
    <div>
      <Navbar color="light" light expand="md">
        <NavbarBrand href="/" className="mr-auto">
          <i>
            <b>kosta.com</b>
          </i>
        </NavbarBrand>
        <NavbarToggler onClick={toggle} className="mr-auto" />
        <Collapse isOpen={isOpen} navbar className="mr-auto">
          <Nav navbar>
            <NavItem>
              <NavLink href="/login"></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/join"></NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/list"></NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
};

export default Main;
